:root {
  --background: #0f172a;
  --foreground: #e2e8f0;
  --primary: #3b82f6;
  --secondary: #1e293b;
  --accent: #06b6d4;
  --muted: #64748b;
  --border: #334155;
}

.dark {
  --background: #0f172a;
  --foreground: #e2e8f0;
  --primary: #3b82f6;
  --secondary: #1e293b;
  --accent: #06b6d4;
  --muted: #64748b;
  --border: #334155;
}

.light {
  --background: #ffffff;
  --foreground: #0f172a;
  --primary: #2563eb;
  --secondary: #f1f5f9;
  --accent: #0891b2;
  --muted: #64748b;
  --border: #e2e8f0;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html,
body {
  max-width: 100vw;
  overflow-x: hidden;
  font-family: 'Inter', sans-serif;
  background-color: var(--background);
  color: var(--foreground);
  scroll-behavior: smooth;
}

body {
  line-height: 1.6;
}

a {
  color: inherit;
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 600;
  line-height: 1.2;
}

.prose {
  max-width: none;
}

